import * as React from "react";
import styled from "@emotion/styled";

interface Item {
  id: string;
  color: string;
  category: string;
  label: string;
  value: string;
}

interface ItemProps {
  item: Item;
}

const Swatch = styled.div(props => ({
  width: 15,
  height: 15,
  margin: 0,
  padding: 0,
  borderRadius: 4,
  backgroundColor: props.color,
}));

const ListItemContainer = styled.div({
  display: "flex",
  flexDirection: "row",
  justifyContent: "space-between",
  alignItems: "center",
});
const ListItem: React.FC<ItemProps> = ({ item }) => {
  return (
    <ListItemContainer>
      <div className="flex">
        <Swatch theme={null} color={item.color} />
        <p>{item.label}</p>
      </div>
      <p>{item.label}</p>
    </ListItemContainer>
  );
};

const ListContainer = styled.section({
  display: "flex",
  flexDirection: "column",
});

interface Props {
  items: Item[];
}
const List: React.FC<Props> = ({ items }) => {
  return (
    <ListContainer>
      {items.map(item => {
        return <ListItem item={item} />;
      })}
    </ListContainer>
  );
};

export default List;
